"use client"

import { useState, useEffect } from "react"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Switch } from "@/components/ui/switch"
import { Label } from "@/components/ui/label"
import Link from "next/link"

interface RideRequest {
  id: string
  pickup: string
  dropoff: string
  distance: string
  estimatedFare: number
  customerName: string
  requestedAt: string
  pickupTime?: string
}

interface DriverStats {
  todayEarnings: number
  weeklyEarnings: number
  totalRides: number
  rating: number
  onlineHours: number
}

interface CurrentRide {
  id: string
  pickup: string
  dropoff: string
  customerName: string
  customerPhone: string
  status: 'accepted' | 'arriving' | 'in-progress'
  fare: number
}

export default function DriverDashboard() {
  const [isOnline, setIsOnline] = useState(false)
  const [availableRides, setAvailableRides] = useState<RideRequest[]>([])
  const [currentRide, setCurrentRide] = useState<CurrentRide | null>(null)
  const [driverStats, setDriverStats] = useState<DriverStats | null>(null)
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    const fetchDriverData = async () => {
      setLoading(true)
      
      // Simulate API calls
      await new Promise(resolve => setTimeout(resolve, 1000))
      
      // Mock driver stats
      const mockStats: DriverStats = {
        todayEarnings: 127.50,
        weeklyEarnings: 892.75,
        totalRides: 234,
        rating: 4.8,
        onlineHours: 6.5
      }

      // Mock available rides
      const mockRides: RideRequest[] = [
        {
          id: "req-001",
          pickup: "Downtown Mall, 456 Oak St",
          dropoff: "Central Station",
          distance: "3.2 km",
          estimatedFare: 12.50,
          customerName: "Sarah Johnson",
          requestedAt: new Date(Date.now() - 2 * 60000).toISOString()
        },
        {
          id: "req-002",
          pickup: "Airport Terminal 2",
          dropoff: "Business District",
          distance: "8.7 km",
          estimatedFare: 28.75,
          customerName: "Mike Chen",
          requestedAt: new Date(Date.now() - 5 * 60000).toISOString()
        },
        {
          id: "req-003",
          pickup: "University Campus",
          dropoff: "Shopping Center",
          distance: "4.1 km",
          estimatedFare: 15.25,
          customerName: "Emma Wilson",
          requestedAt: new Date(Date.now() - 1 * 60000).toISOString()
        }
      ]

      // Mock current ride (sometimes null)
      const mockCurrentRide: CurrentRide | null = Math.random() > 0.7 ? {
        id: "ride-active-001",
        pickup: "City Center Plaza",
        dropoff: "Residential Area",
        customerName: "John Doe",
        customerPhone: "+1 (555) 987-6543",
        status: "arriving",
        fare: 18.50
      } : null

      setDriverStats(mockStats)
      setAvailableRides(mockRides)
      setCurrentRide(mockCurrentRide)
      setLoading(false)
    }

    fetchDriverData()

    // Set up polling for new ride requests
    const interval = setInterval(() => {
      if (isOnline) {
        fetchDriverData()
      }
    }, 15000) // Poll every 15 seconds when online

    return () => clearInterval(interval)
  }, [isOnline])

  const handleAcceptRide = async (rideId: string) => {
    try {
      // Simulate API call to accept ride
      await new Promise(resolve => setTimeout(resolve, 1000))
      
      // Remove from available rides and set as current ride
      const acceptedRide = availableRides.find(ride => ride.id === rideId)
      if (acceptedRide) {
        setCurrentRide({
          id: acceptedRide.id,
          pickup: acceptedRide.pickup,
          dropoff: acceptedRide.dropoff,
          customerName: acceptedRide.customerName,
          customerPhone: "+1 (555) 123-4567", // Mock phone
          status: "accepted",
          fare: acceptedRide.estimatedFare
        })
        setAvailableRides(prev => prev.filter(ride => ride.id !== rideId))
      }
    } catch (error) {
      console.error('Failed to accept ride:', error)
    }
  }

  const getTimeAgo = (dateString: string) => {
    const now = new Date()
    const past = new Date(dateString)
    const diffMinutes = Math.floor((now.getTime() - past.getTime()) / 60000)
    
    if (diffMinutes < 1) return 'Just now'
    if (diffMinutes === 1) return '1 minute ago'
    return `${diffMinutes} minutes ago`
  }

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'accepted': return 'bg-blue-100 text-blue-800'
      case 'arriving': return 'bg-orange-100 text-orange-800'
      case 'in-progress': return 'bg-green-100 text-green-800'
      default: return 'bg-gray-100 text-gray-800'
    }
  }

  if (loading) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <div className="w-8 h-8 border-4 border-black border-t-transparent rounded-full animate-spin mx-auto mb-4"></div>
          <p className="text-gray-600">Loading driver dashboard...</p>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gray-50">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Header */}
        <div className="mb-8">
          <div className="flex justify-between items-center">
            <div>
              <h1 className="text-3xl font-bold text-black mb-2">Driver Dashboard</h1>
              <p className="text-gray-600">Manage your rides and earnings</p>
            </div>
            <div className="flex items-center space-x-4">
              <div className="flex items-center space-x-2">
                <Label htmlFor="online-status">Go Online</Label>
                <Switch
                  id="online-status"
                  checked={isOnline}
                  onCheckedChange={setIsOnline}
                />
              </div>
              <Badge className={isOnline ? 'bg-green-100 text-green-800' : 'bg-gray-100 text-gray-800'}>
                {isOnline ? 'Online' : 'Offline'}
              </Badge>
            </div>
          </div>
        </div>

        {/* Stats Cards */}
        {driverStats && (
          <div className="grid grid-cols-2 md:grid-cols-5 gap-4 mb-8">
            <Card>
              <CardContent className="pt-6">
                <div className="text-center">
                  <p className="text-2xl font-bold text-black">${driverStats.todayEarnings.toFixed(2)}</p>
                  <p className="text-sm text-gray-600">Today's Earnings</p>
                </div>
              </CardContent>
            </Card>
            <Card>
              <CardContent className="pt-6">
                <div className="text-center">
                  <p className="text-2xl font-bold text-black">${driverStats.weeklyEarnings.toFixed(2)}</p>
                  <p className="text-sm text-gray-600">This Week</p>
                </div>
              </CardContent>
            </Card>
            <Card>
              <CardContent className="pt-6">
                <div className="text-center">
                  <p className="text-2xl font-bold text-black">{driverStats.totalRides}</p>
                  <p className="text-sm text-gray-600">Total Rides</p>
                </div>
              </CardContent>
            </Card>
            <Card>
              <CardContent className="pt-6">
                <div className="text-center">
                  <p className="text-2xl font-bold text-black">{driverStats.rating}</p>
                  <p className="text-sm text-gray-600">Rating ‚òÖ</p>
                </div>
              </CardContent>
            </Card>
            <Card>
              <CardContent className="pt-6">
                <div className="text-center">
                  <p className="text-2xl font-bold text-black">{driverStats.onlineHours}h</p>
                  <p className="text-sm text-gray-600">Online Today</p>
                </div>
              </CardContent>
            </Card>
          </div>
        )}

        <div className="grid lg:grid-cols-3 gap-8">
          {/* Current Ride */}
          <div className="lg:col-span-1">
            <h2 className="text-xl font-semibold mb-4">Current Ride</h2>
            {currentRide ? (
              <Card className="border-2 border-green-200">
                <CardHeader>
                  <div className="flex justify-between items-start">
                    <div>
                      <CardTitle className="text-lg">Active Ride</CardTitle>
                      <CardDescription>#{currentRide.id.slice(-6)}</CardDescription>
                    </div>
                    <Badge className={getStatusColor(currentRide.status)}>
                      {currentRide.status === 'accepted' ? 'Accepted' : 
                       currentRide.status === 'arriving' ? 'Arriving' : 'In Progress'}
                    </Badge>
                  </div>
                </CardHeader>
                <CardContent>
                  <div className="space-y-3">
                    <div>
                      <p className="text-sm text-gray-600">Customer</p>
                      <p className="font-medium">{currentRide.customerName}</p>
                    </div>
                    <div>
                      <p className="text-sm text-gray-600">From</p>
                      <p className="font-medium">{currentRide.pickup}</p>
                    </div>
                    <div>
                      <p className="text-sm text-gray-600">To</p>
                      <p className="font-medium">{currentRide.dropoff}</p>
                    </div>
                    <div className="flex justify-between items-center pt-2 border-t">
                      <span className="text-lg font-semibold">${currentRide.fare.toFixed(2)}</span>
                      <Link href={`/driver/ride/${currentRide.id}`}>
                        <Button size="sm" className="bg-black hover:bg-gray-800 text-white">
                          Manage Ride
                        </Button>
                      </Link>
                    </div>
                  </div>
                </CardContent>
              </Card>
            ) : (
              <Card>
                <CardContent className="text-center py-12">
                  <div className="w-16 h-16 bg-gray-100 rounded-full mx-auto mb-4 flex items-center justify-center">
                    <span className="text-gray-400 text-2xl">üöó</span>
                  </div>
                  <p className="text-gray-600 mb-2">No active ride</p>
                  <p className="text-sm text-gray-500">
                    {isOnline ? 'Waiting for ride requests...' : 'Go online to receive ride requests'}
                  </p>
                </CardContent>
              </Card>
            )}
          </div>

          {/* Available Rides */}
          <div className="lg:col-span-2">
            <div className="flex justify-between items-center mb-4">
              <h2 className="text-xl font-semibold">Available Rides</h2>
              <Badge variant="outline">
                {availableRides.length} requests
              </Badge>
            </div>
            
            {!isOnline ? (
              <Card>
                <CardContent className="text-center py-12">
                  <div className="w-16 h-16 bg-gray-100 rounded-full mx-auto mb-4 flex items-center justify-center">
                    <span className="text-gray-400 text-2xl">üì±</span>
                  </div>
                  <p className="text-gray-600 mb-2">You're offline</p>
                  <p className="text-sm text-gray-500">Turn on "Go Online" to start receiving ride requests</p>
                </CardContent>
              </Card>
            ) : availableRides.length === 0 ? (
              <Card>
                <CardContent className="text-center py-12">
                  <div className="w-16 h-16 bg-gray-100 rounded-full mx-auto mb-4 flex items-center justify-center">
                    <span className="text-gray-400 text-2xl">‚è≥</span>
                  </div>
                  <p className="text-gray-600 mb-2">No ride requests</p>
                  <p className="text-sm text-gray-500">New requests will appear here automatically</p>
                </CardContent>
              </Card>
            ) : (
              <div className="space-y-4">
                {availableRides.map((ride) => (
                  <Card key={ride.id} className="hover:shadow-md transition-shadow">
                    <CardContent className="pt-6">
                      <div className="flex justify-between items-start mb-4">
                        <div className="flex-1">
                          <div className="flex justify-between items-center mb-2">
                            <h3 className="font-semibold">{ride.customerName}</h3>
                            <span className="text-lg font-bold">${ride.estimatedFare.toFixed(2)}</span>
                          </div>
                          <p className="text-sm text-gray-600 mb-1">Distance: {ride.distance}</p>
                          <p className="text-xs text-gray-500">{getTimeAgo(ride.requestedAt)}</p>
                        </div>
                      </div>
                      
                      <div className="space-y-2 mb-4">
                        <div>
                          <p className="text-sm text-gray-600">Pickup</p>
                          <p className="font-medium text-sm">{ride.pickup}</p>
                        </div>
                        <div>
                          <p className="text-sm text-gray-600">Drop-off</p>
                          <p className="font-medium text-sm">{ride.dropoff}</p>
                        </div>
                      </div>
                      
                      <div className="flex gap-2">
                        <Button 
                          className="flex-1 bg-black hover:bg-gray-800 text-white"
                          onClick={() => handleAcceptRide(ride.id)}
                          disabled={!!currentRide}
                        >
                          Accept Ride
                        </Button>
                        <Button 
                          variant="outline" 
                          size="sm"
                          className="border-gray-300"
                        >
                          Details
                        </Button>
                      </div>
                    </CardContent>
                  </Card>
                ))}
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  )
}
